import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { addToCart } from '../store/CartSlice'



function Form() {

    const [inputValue, setInputValue] = useState("")
    const dispatch = useDispatch()

    const handleSubmit = (e) => {
        e.preventDefault()
        dispatch(addToCart(inputValue))
        setInputValue("")
    }
    const handleClick=()=>{

    }
   
    return (
        <>
        <form className="todoform" onSubmit={(e) => handleSubmit(e)} >
            <input type="text" className="inputform" value={inputValue} onChange={(e) => setInputValue(e.target.value)} placeholder="Enter your To-Do" required />
            <button type="submit" className="submit-btn" onClick={()=>{handleClick()}} >Submit</button>
        </form>
        </>
    )
}

export default Form